body {
  background-color: blueviolet;
}

.contain {
  width: 10.5em;
  margin: 0 auto;
  background-color: aliceblue;
}

.header {
  margin-bottom: 0.8em;
  padding: 0.4em 2em 0.4em 1em;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.15);
  font-weight: 500;
  font-size: 1.3rem;
  color: #3faffa;
}

.list-container {
  position: absolute;
  z-index: 100;
  width: 10.5em;
}

.list {
  padding: 0;
  margin: 0;
  padding-left: 1em;
  background: #ffffff;
  border: 2px solid #e5e5e5;
  box-sizing: border-box;
  color: #3faffa;
  font-size: 1.3rem;
  font-weight: 500;


}

.list-item {
  list-style: none;
  margin-bottom: 0.8em;
  cursor: pointer;
}

div {
  cursor: pointer;
}